@import "../../../utils/utils.scss";

.tickler-day-wrapper {
  width: 100%;
  min-height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: hidden;
  border-right: $calendar-lines;
  border-bottom: $calendar-lines;
  width: 100%;
  header {
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;

    .days-names {
      margin-top: 0.25rem;
      font-size: 0.875rem;
      line-height: 1.25rem;
    }

    .days-numbers {
      width: 1.5rem;
      height: 1.5rem;
      margin-top: 0.25rem;
      margin-bottom: 0.25rem;
      padding: 0.25rem;
      line-height: 1.25rem;
      display: flex;
      justify-content: center;
      align-items: center;

      cursor: pointer;
      &:hover {
        background-color: rgba(0, 0, 0, 0.205);
        border-radius: 100%;
      }
    }

    .current-day {
      background-color: $selection-accent;
      border-radius: 100%;
      &:hover {
        background-color: $selection-accent-hover;
      }
    }
  }

  .dayEvents-wrapper {
    width: 100%;
    padding-right: 0.25rem;
    height: fit-content;

    .more {
      width: fit-content;
      cursor: pointer;
    }
  }
}
