@import "../../utils/utils.scss";
@import "../../utils/utils.scss";

.label-wrapper {
  p {
    font-weight: 700;
    color: rgb(107 114 128);
  }

  .list-wrapper {
    display: grid;

    label {
      align-items: center;
      margin-top: 0.75rem;
      display: block;
      position: relative;
      padding-left: 1.5rem;
      margin-bottom: 0.75rem;
      cursor: pointer;
      user-select: none;
      text-transform: capitalize;

      input {
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;
        &:checked ~ .checkMark {
          &:after {
            display: block;
          }
        }
      }

      .checkMark {
        position: absolute;
        bottom: calc(50% - 0.625rem);
        left: 0;
        height: 1.25rem;
        width: 1.25rem;
        border-width: 2px;
        border-style: solid;
        border-radius: 0.25rem;

        &:after {
          content: "";
          position: absolute;
          display: none;
          left: 6px;
          top: 3px;
          width: 5px;
          height: 10px;
          border: solid white;
          border-width: 0 3px 3px 0;
          transform: rotate(45deg);
        }
      }

      &:hover {
        input {
          &:checked {
            & ~ .checkMark {
              &:after {
                display: block;
              }
            }
          }
        }
      }
    }

    .state-filter {
      display: flex;
      justify-content: space-around;
      padding: 1rem 0;

      button {
        $state-filter-button-color: #4d00e7;
        margin: 0 0.3rem;
        width: 100%;
        padding: 0.1rem 0.5rem;
        border-radius: 1rem;
        border: 0.1rem solid $state-filter-button-color;
        color: $state-filter-button-color;
        transition: background-color 0.4s ease;

        &:hover {
          background-color: #cb6ce6;
          color: #ffffff;
        }
        &:active {
          background-color: #8c52ff;
          color: #fff;
        }
      }
    }
  }
}

input {
  &:checked ~ .text-indigo {
    background-color: $Indigo-500;
  }
}

input {
  &:checked ~ .text-gray {
    background-color: $Gray-500;
  }
}

input {
  &:checked ~ .text-green {
    background-color: $Green-500;
  }
}

input {
  &:checked ~ .text-blue {
    background-color: $Blue-500;
  }
}

input {
  &:checked ~ .text-red {
    background-color: $Red-500;
  }
}

input {
  &:checked ~ .text-purple {
    background-color: $Purple-500;
  }
}

.text-indigo {
  border-color: $Indigo-500;
}
.text-gray {
  border-color: $Gray-500;
}
.text-green {
  border-color: $Green-500;
}
.text-blue {
  border-color: $Blue-500;
}
.text-red {
  border-color: $Red-500;
}
.text-purple {
  border-color: $Purple-500;
}
