@import "../../utils/utils.scss";

.menuModal_wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  span {
    color: $secondary-light;
  }

  .menuModal {
    position: absolute;
    z-index: 200;
    width: 25rem;
    height: 35rem;
    background-color: rgb(255 255 255);
    border-radius: 1rem;
    .menuModal__header {
      display: flex;
      justify-content: space-between;
      background-color: rgb(243 244 246);
      padding: 0.5rem 1rem;
      border-radius: 1rem 1rem 0 0;
      span {
        color: rgb(96, 96, 96);
      }
      div {
        width: 100%;
        display: flex;
        justify-content: end;
        button {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-right: 0.5rem;
        }
      }
    }

    .menuModal__body {
      width: 100%;
      padding: 1rem;

      .body-wrapper {
        .toInboxButtons {
          width: 100%;
          display: flex;
          justify-content: space-around;
          margin: 0.5rem 0;
          button {
            border-radius: 2rem;
            border: 1px solid black;
            padding: 0.1rem 1rem;
            background-color: $add-button-color;
            &:focus {
              background-color: $primary-color;
              color: white;
              border: none;
            }
          }
        }
        .title-input {
          width: 100%;
          padding-top: 0.75rem;
          border-width: 0px;
          color: rgb(75 85 99);
          font-size: 1.25rem /* 20px */;
          line-height: 1.75rem /* 28px */;
          font-weight: 600;
          padding-bottom: 0.5rem /* 8px */;
          border-bottom-width: 2px;
          border-color: rgb(229 231 235);
          outline: 2px solid transparent;
          outline-offset: 2px;

          &:focus {
            outline: 2px solid transparent;
            outline-offset: 2px;
            border-color: rgb(59 130 246);
          }
        }

        .description-input {
          width: 100%;
          border-width: 0px;
          border-bottom-width: 2px;
          padding-top: 0.75rem;
          padding-bottom: 0.5rem;
          color: rgb(75 85 99);
          border-color: rgb(229 231 235);

          &:focus {
            outline: 2px solid transparent;
            outline-offset: 2px;
            border-color: rgb(59 130 246);
          }
        }

        .actions {
          padding: 0 1rem;
          display: flex;
          flex-direction: column;

          .add-action-wrapper {
            width: 100%;
          }

          .input-wrapper {
            display: flex;
            justify-content: flex-start;
            align-items: center;

            button {
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }
        }
      }
    }
  }

  .colors-input {
    grid-column-start: 2;
    display: flex;
    column-gap: 0.5rem;

    .color {
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 100%;
      border: 1px solid rgb(8, 8, 8);
      box-shadow: rgba($color: #000000, $alpha: 0.5) 0px 0px 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      cursor: pointer;
    }

    span {
      color: rgb(255 255 255);
      font-size: 0.875rem;
      line-height: 1.25rem;
    }
  }

  footer {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    border-top-width: 1px;
    padding: 0.75rem;
    margin-top: 1.25rem;

    button {
      background-color: rgb(59 130 246);
      padding: 0.5rem 1.5rem;
      border-radius: 0.25rem;
      border: 1px solid black;
      box-shadow: rgba($color: #000000, $alpha: 0.5);
      color: rgb(255 255 255);

      &:hover {
        background-color: rgb(37 99 235);
      }
    }
  }

  .card_modal {
    position: absolute;
    z-index: 200;
    width: 25rem;
    height: 35rem;
    border-radius: 1rem;
    .input-wrapper {
      width: 100%;
      input {
        width: calc(100% - 3rem);
        &:nth-child(1) {
          margin-top: 1rem;
        }
      }
    }
  }
}

.date-selector {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  .miniCalendar-section {
    position: relative;
    .miniCalendar-wrapper {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .time {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 8rem;

    input {
      margin: 0 0.5rem 0 0.5rem;
      width: 2.5rem;
      &:focus {
        border: none;
        outline: none;
      }
      &:hover {
        cursor: pointer;
        background-color: darkgray;
        outline: 1px solid rgb(102, 102, 102);
        border-radius: 10%;
      }
    }
  }
}

.error {
  color: red;
  font-size: small;
}

@media (max-width: 440px) {
  .menuModal_wrapper {
    .menuModal {
      position: absolute;
      z-index: 200;
      width: 100%;
      height: 100%;
      background-color: rgb(255 255 255);
      border-radius: 0px;
      form {
        height: 100%;
      }
      .menuModal__header {
        display: flex;
        justify-content: space-between;
        background-color: rgb(243 244 246);
        padding: 0.5rem 1rem;
        border-radius: 0px;
        height: 3rem;
      }
      .menuModal__body {
        width: 100%;
        height: calc(100% - 3rem);
        padding: 1rem;
        .body-wrapper {
          height: 100%;
          .title-input {
            width: 100%;
            padding-top: 0.75rem;
            border-width: 0px;
            color: rgb(75 85 99);
            font-size: 1.25rem /* 20px */;
            line-height: 1.75rem /* 28px */;
            font-weight: 600;
            padding-bottom: 0.5rem /* 8px */;
            border-bottom-width: 2px;
            border-color: rgb(229 231 235);
            outline: 2px solid transparent;
            outline-offset: 2px;

            &:focus {
              outline: 2px solid transparent;
              outline-offset: 2px;
              border-color: rgb(59 130 246);
            }
          }

          .description-input {
            width: 100%;
            height: calc(100% - (3rem + 50px + 67px + 24px + 26px));
            min-height: 100px;
            border-width: 0px;
            border-bottom-width: 2px;
            padding-top: 0.75rem;
            padding-bottom: 0.5rem;
            color: rgb(75 85 99);
            border-color: rgb(229 231 235);

            &:focus {
              outline: 2px solid transparent;
              outline-offset: 2px;
              border-color: rgb(59 130 246);
            }
          }

          .actions {
            padding: 0 1rem;
            display: flex;
            flex-direction: column;

            .add-action-wrapper {
              width: 100%;
            }

            .input-wrapper {
              display: flex;
              justify-content: flex-start;
              align-items: center;

              button {
                display: flex;
                justify-content: center;
                align-items: center;
              }
            }
          }
        }
      }
    }
  }
}
