@import "../../../utils/utils.scss";

.eventsView-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .eventsView {
    background-color: $secondary-color;
    width: 25rem;
    height: 40rem;
    border-radius: 1rem;
    box-shadow: black 0px 0px 2rem;
    .form__header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: $secondary-light;
      padding: 0.5rem 1rem;
      border-radius: 1rem 1rem 0 0;
      box-shadow: $box-shadow;

      .navigation-arrows {
        display: flex;
        justify-content: center;
        align-items: center;

        .navigation-arrows__container {
          display: flex;

          button {
            display: flex;
            justify-content: center;
            align-items: center;
          }
          .arrow {
            cursor: pointer;
            color: rgb(75 85 99);
          }
        }

        h2 {
          font-weight: 700;
          color: rgb(107 114 128);
        }
      }
    }

    .eventsView_body {
      padding: 1rem 0.5rem;
      overflow-y: auto;
      scrollbar-width: none;
      -ms-overflow-style: none;
      display: grid;
      justify-items: center;
      height: calc(100% - 40px - 0.5rem);
      grid-template-columns: 1fr 1fr;
      gap: 1rem;
      @include barStyles();

      &::-webkit-scrollbar-track {
        background: opacity($color: #000000);
      }

      &::-webkit-scrollbar:hover {
        display: block;
      }

      .card {
        $thisCardHight: 15rem;
        width: 11rem;
        height: $thisCardHight;

        .card__description {
          height: calc($thisCardHight - 5rem);
        }
      }

      .addButton-wrapper {
        margin-top: 1rem;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        button {
          border-radius: 100%;
          display: flex;
          justify-content: center;
          align-items: center;
          background-color: $add-button-color;
        }
      }
    }
  }
}
