$primary-color: #5c3dff;
$secondary-color: #353535;
$secondary-light: #d9d9d9;
$base-color: #ffffff;
$highlights: #f9e900;
$menu: whitesmoke;
$underline: black;
$action-button: rgb(59, 130, 246);
$selection-accent-hover: rgb(110 34 180);
$selection-accent: blueviolet;
$dayview-background: white;
$timeline-color: red;
$menu-button-color: black;
$text-color-light: white;
$add-button-color: whitesmoke;
$text-color-dark: black;

$calendar-lines: 1px solid #ef00ff;
$background: linear-gradient(179deg, rgb(11 0 35) 30%, rgb(20 0 65) 100%);

$headerHeight: 3rem;
$bodyHeight: calc(100vh - $headerHeight);
$box-shadow: $secondary-color 0px 0px 5px;

@mixin barStyles {
  &:hover {
    &::-webkit-scrollbar-track {
      background: rgba($color: #000000, $alpha: 0.125);
    }

    &::-webkit-scrollbar-thumb {
      background: rgba($color: #000000, $alpha: 0.5);
    }
  }
}

@mixin aibutton {
  background-color: $action-button;
  border-radius: 100%;
  width: 2rem;
  height: 2rem;
  border: 1px solid black;
  color: $text-color-light;
  &.disabled {
    background-color: rgb(128, 128, 128);
    border: none;
    color: rgb(196, 196, 196);
    cursor: not-allowed;
  }
}

