.editOptions-wrapper {
	display: flex;
}

.card {
	textarea[type="text"] {
		background-color: rgba($color: #000000, $alpha: 0);
		width: 100%;
		height: 100%;
		resize: none;
	}

	&__title {
		input[type="text"] {
			background-color: rgba($color: #000000, $alpha: 0);
			text-align: center;
		}
	}
}
