@import "../../utils/utils.scss";

.ticklerFile_wrapper {
  position: relative;
  background-color: rgb(11, 0, 35);

  .ticklerFile__body-wrapper {
    height: $bodyHeight;
    display: grid;
    grid-template-columns: 1fr 10fr;
    grid-gap: 0.5rem;

    .ticklerFile_months-wrapper {
      width: 100%;
      height: 100%;
      display: grid;
      background-color: $secondary-light;
      grid-template-rows: repeat(12, 1fr);
      .month {
        display: flex;
        padding: 0.5rem;
        width: 100%;
        button {
          text-transform: capitalize;
        }
      }

      .monthSelected {
        border-left: 0.5rem solid #ef00ff;
        background: linear-gradient(270deg, rgb(11 0 35) 30%, rgb(20 0 65) 100%);
        button {
          color: $text-color-light;
        }
      }

      .thisMonth {
        color: $selection-accent;
      }
    }

    .ticklerFile_days-wrapper {
      height: $bodyHeight;
      width: 100%;
      display: grid;
      grid-template-columns: repeat(7, 1fr);
      grid-auto-flow: row;
      grid-gap: 1rem;
      color: $secondary-light;
      overflow-y: auto;
    }
  }
}

//medias

//tablet-SmLaptop
@media (max-width: 1024px) {
  .ticklerFile_wrapper {
    .ticklerFile__body-wrapper {
      .ticklerFile_days-wrapper {
        grid-template-columns: repeat(5, 1fr);
      }
    }
  }
}

@media (max-width: 440px) {
  .ticklerFile_wrapper {
    .ticklerFile__body-wrapper {
      .ticklerFile_days-wrapper {
        grid-template-columns: repeat(3, 1fr);
      }
    }
  }
}
